.block10 {
  margin-top: 65px;

  @media (min-width: $tablet-width) {
    margin-top: 10px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 105px;
  }
}

.block10__bg {
  position: relative;
  height: 489px;
  margin: 0 auto;

  background: #ffffff;
  //background: red;
  background-image: url("../img/bg-block10-mob@1x.jpg");
  background-repeat: no-repeat;
  background-size: auto 427px;
  background-position: -335px 10px;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/bg-block10-mob@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    height: 708px;

    background-image: url("../img/bg-block10@1x.jpg");
    background-repeat: no-repeat;
    background-size: auto 599px;
    background-position: -115px 25px;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url("../img/bg-block10@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    height: 705px;

    background-image: url("../img/bg-block10@1x.jpg");
    background-position: center;
    background-size: auto 599px;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url("../img/bg-block10@2x.jpg");
    }
  }

  &::before {
    /*маска-рамка для фона*/
    content: "";
    position: absolute;
    z-index: 30;
    top: -10px;
    left: 0;

    width: 100%;
    height: 100%;
    background-image: url("../img/bg-block10-mask-mob@1x.png");
    background-repeat: repeat-x;
    background-size: auto 100%;
    background-position: center;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background-image: url("../img/bg-block10-mask-mob@2x.png");
    }

    @media (min-width: $tablet-width) {
      top: -20px;
      background-repeat: no-repeat;
      background-size: 100% 100%;

      background-image: url("../img/bg-block10-mask-tab@1x.png");

      @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url("../img/bg-block10-mask-tab@2x.png");
      }
    }

    @media (min-width: $desktop-width) {
      top: -10px;
      background-image: url("../img/bg-block10-mask-desk@1x.png");
      background-repeat: no-repeat;
      background-size: 100% 100%;

      @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
        background-image: url("../img/bg-block10-mask-desk@2x.png");
      }
    }
  }

  &::after {
    /*этот элемент закрывает прозрачный фон сверху маски-рамки*/
    content: "";
    position: absolute;
    z-index: 30;
    top: 0;
    left: 0;
    width: 100%;
    height: 20px;
    background: #ffffff;

    @media (min-width: $tablet-width) {
      content: "";
      position: absolute;
      z-index: 30;
      top: 0;
      left: 0;
      width: 100%;
      height: 40px;
      background: #ffffff;
    }

    @media (min-width: $desktop-width) {
      content: "";
      position: absolute;
      z-index: 30;
      top: 0;
      left: 0;
      width: 100%;
      height: 40px;
      background: #ffffff;
    }
  }
}
